import Vue from 'vue'
import Router from 'vue-router'
const Inicio = () => import('./views/Inicio')
const Busqueda = () => import('./views/Busquedas')
const Ventas = () => import('./views/Ventas')
const Total = () => import('./views/Total')
const NotFound = () => import('./views/NotFound')
const Lista = () => import('./components/ListadoDeJuegos.vue')

Vue.use(Router)

export default new Router({
    mode: 'history', 
    routes: [
        {
            path: '/32Bits/',
            name: 'inicio',
            component: Inicio
        },     
        {
            path: '/32Bits/busqueda/',
            name: 'busqueda',
            component: Busqueda,
            props: true,
            children:[{
                path:'lista',
                name:'lista',
                component: Lista
            }]
        },     
        {
            path: '/32Bits/ventas',
            name: 'ventas',
            component: Ventas
        },     
        {
            path: '/32Bits/total',
            name: 'total',
            component: Total
        },     
        {
            path: '/32Bits/*',
            name: 'notFound',
            component: NotFound
        },     
    ]
})