import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    juegos:[
      
      {
        codigo: '0001',
        nombre: 'Sekiro: Shadows die twice',
        img: 'https://as.com/meristation/imagenes/2019/02/12/avances/1549991406_429548_1550039476_noticia_normal.jpg',
        stock: 100,
        precio: 30000,
        color: 'red',
        destacado: true
      },
      {
        codigo: '0002',
        nombre: 'Fifa 21',
        img: 'https://sm.ign.com/t/ign_es/screenshot/default/ea8bc81e-23e4-4317-aaf2-cc17917236cf_f6d7.1280.jpg',
        stock: 100,
        precio: 25000,
        color: 'blue',
        destacado: false
      },
      {
        codigo: '0003',
        nombre: 'Gears of War 4',
        img: 'https://gepig.com/game_cover_460w/3560.jpg',
        stock: 100,
        precio: 15000,
        color: 'green',
        destacado: true
      },
      {
        codigo: '0004',
        nombre: 'Mario Tennis Aces',
        img: 'https://www.nintendo.com//content/dam/noa/es_LA/games/switch/m/mario-tennis-aces-switch/mario-tennis-aces-switch-hero.jpg',
        stock: 100,
        precio: 35000,
        color: 'yellow',
        destacado: false
      },
      {
        codigo: '0005',
        nombre: 'Bloodborne',
        img: 'https://cdn.hobbyconsolas.com/sites/navi.axelspringer.es/public/styles/480/public/media/image/2021/06/bloodborne-2380913.jpg?itok=c2EEfzjK',
        stock: 100,
        precio: 10000,
        color: 'blue',
        destacado: false
      },
      {
        codigo: '0006',
        nombre: 'Forza Horizon 4',
        img: 'https://cdn.akamai.steamstatic.com/steam/apps/1293830/extras/FH4_Deluxe_TitledHero_HD_1920x1080.png?t=1622068013',
        stock: 100,
        precio: 20000,
        color: 'red',
        destacado: true
      },
      {
        codigo: '0007',
        nombre: 'Horizon Zero',
        img: 'https://cdn.akamai.steamstatic.com/steam/apps/1293830/extras/FH4_Deluxe_TitledHero_HD_1920x1080.png?t=1622068013',
        stock: 0,
        precio: 30000,
        color: 'blue',
        destacado: true
      },
    ],
    juegosConStock:[],
    ventas:[],
    total: 0,
  },
  getters: {
    filtrarPorCodigo:(state)=>(cod)=>{
      return state.juegos.find((juego)=>{
        return juego.codigo == cod
      })
    },
    cantidadDeJuegos:(state)=>{
      return state.juegos.length
    },
    cantidadDeStock:(state)=>{
      let totalStock = 0
      for (let i = 0; i < state.juegos.length; i++) {
        const stock = state.juegos[i].stock;
        totalStock = totalStock + stock
      }
      return totalStock
    },

  },
  mutations:{
    conStock:(state)=>{
      state.juegosConStock = state.juegos.filter(juego => juego.stock > 0)
    },
    restarStock:(state, juego)=>{
      state.juegosConStock[juego].stock--
    },
    registrarVenta:(state, juego)=>{
      state.ventas.push({
        codigo: juego.codigo,
        nombre: juego.nombre,
        precio: juego.precio
      })
    },
    sumarTotal:(state, juego)=>{
      state.total = parseInt(state.total + juego.precio)
    },
    restarPorCodigo(state, codigo){
      for (let i = 0; i < state.juegosConStock.length; i++) {
        const juego= state.juegosConStock[i];
        if (juego.codigo == codigo) {
          juego.stock--
        }
        
      }
    }
  },
  actions: {
    actualizarStock:(context)=>{
      context.commit('conStock')
    },
    async venderProducto(context, juego){
      await setTimeout(
        ()=>{
          context.commit('restarStock', juego)}
      , 1000)
    },
    async agregarVenta(context, juego){
      await setTimeout(() => {
        context.commit('registrarVenta', juego)
      }, 1000);
      
    },
    totalVentas:(context, juego)=>{
      context.commit('sumarTotal', juego)
    },
  }
});

export default store;
